import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import CurrencyCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/currency/currency/actions";
import { Box } from "../../../App/app.style";

const redirectTo = '/dashboard/setup/currencies'

class CreateCurrency extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createCurrency } = this.props;
    createCurrency();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.currency.error) {
      notification("error", this.props.currency.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.currency.success) {
      this.props.history.replace(redirectTo);
    }
  }

  handleSubmit = value => {
    const { storeCurrency } = this.props;
    storeCurrency(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="currencies.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CurrencyCreateForm
            submit={this.handleSubmit}
            currency={this.props.currency}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { currency: state.currency };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateCurrency);
